body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td,
menu {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img,
abbr {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul li,
menu li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
legend {
  color: #000;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
img {
  max-width: 100%;
  height: auto;
}
/* styles */
.wrap {
  max-width: 960px;
  margin: 0 auto;
  height: 100%;
  padding: 0 5px;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
}
@media screen and (max-width: 841px) {
  .wrap div:nth-child(even) {
    flex-direction: row-reverse;
  }
}
section.profit {
  background: #232324;
  padding: 15px 0;
}
section.profit .item {
  border: 3px solid #fff;
  text-align: center;
  background: #fff;
  display: flex;
  flex-wrap: wrap;
  flex-basis: 400px;
  margin: 5px;
  justify-content: space-between;
  flex-grow: 1;
}
section.profit .item .text {
  text-align: left;
  font-style: italic;
  margin-left: 10px;
  flex-basis: 290px;
  flex-grow: 3;
}
/* positions */
.img {
  flex-basis: 100px;
  flex-grow: 1;
}
